/* global FB */
import React, { useEffect, useState } from "react";
import { Box, Button, Menu, Text, Footer, Grommet } from "grommet";
import "../pages/index.css";
import "./i18n";
import { assign } from "../helpers";
import { Tools } from "grommet-icons";
import { navigate } from "gatsby-link";
import Parse from "../library/parse";
import { storeLoginDetails } from "../state/actions";
import Loading from "../components/Loading";

const theme = {
  tab: {
    active: "red",
  },
  global: {
    font: {
      family: "Roboto",
      size: "18px",
      height: "20px",
    },
  },
};

const Home = ({ children }) => {
  const [accountMenu, setAccountMenu] = useState([]);
  const [username, setUsername] = useState("Guest");
  const [allowStore, setAllowStore] = useState(false);
  useEffect(() => {
    const user = Parse.User.current();
    user ? setUsername(user.get("email")) : setUsername("Guest");
    if (user && user.get("allowStore")) {
      setAllowStore(true);
    }
  }, [children]);

  useEffect(() => {
    const logoutMenu = [
      {
        label: "Dashboard",
        onClick: () => navigate("/dashboard"),
      },
      {
        label: "Sign Out",
        onClick: () => {
          Parse.User.logOut().then(() => {
            setUsername("Guest");
            navigate("/");
          });
        },
      },
    ];

    allowStore &&
      logoutMenu.push({
        label: "My Store",
        onClick: () => {
          navigate("/account/store");
        },
      });
      allowStore &&
      logoutMenu.push({
        label: "System",
        onClick: () => navigate("/system"),
      });
      
    const accMenu =
      username === "Guest"
        ? [
            {
              label: "Sign in",
              onClick: async () => {
                navigate("/login");
              },
            },
          ]
        : logoutMenu;
    setAccountMenu(accMenu);
  }, [username]);

  return (
    <Grommet theme={theme}>
      <Box>
        <Box
          gridArea="header"
          direction="row"
          align="center"
          pad={{ horizontal: "medium", vertical: "small" }}
          background="brand"
        >
          <Box>
            <Button
              icon={<Tools />}
              label="BidBid"
              margin={{ right: "20px" }}
              onClick={() => {
                assign("/");
              }}
            />
          </Box>
          <Box margin={{ left: "auto" }}>
            <Menu label={username} size="small" items={accountMenu} />
          </Box>
        </Box>

        <Box align="center" justify="center" pad="small">
          <Box
            pad={{
              bottom: "64px",
            }}
            fill="horizontal"
            width={{
              min: "360px",
            }}
          >
            {children}
            <Loading />
          </Box>
        </Box>
        <Footer margin={{ top: "auto" }} background="brand" pad="medium">
          <Text>Copyright {new Date().getFullYear()} </Text>
        </Footer>
      </Box>
    </Grommet>
  );
};

export default Home;
